.division-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin-bottom: 2rem;
}

.division-buttons button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border: none;
  border-radius: 6px;
  background-color: #444;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.division-buttons button:hover {
  background-color: #666;
}

.division-buttons .active {
  background-color: #0077cc;
}

.division-content {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
}

.division-block h3 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.division-block p {
  font-size: 1.2rem;
  line-height: 1.6;
}

/* Optional fade-in animation */
.fade-in {
  animation: fadeIn 0.4s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
