/* Reset default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica Neue', sans-serif;
  color: white;
  background: linear-gradient(to bottom right, #133187, #6f1e56);
  height: 100vh;
  overflow: hidden;
}

/* Transparent nav bar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  justify-content: flex-end;
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1000;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 30px;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

.navbar a:hover {
  color: #e38a16;
}

/* Hero section */
.hero {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 20px;
}

.hero h1 {
  font-size: 4rem;
  margin-bottom: 20px;
}

.tagline {
  font-size: 1.5rem;
  max-width: 600px;
  margin-bottom: 10px;
}

.location {
  font-size: 1rem;
  color: #757575;
}

.page {
  min-height: 100vh;
  padding-top: 120px; /* Push content below navbar */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
}

.page h2 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.page p {
  font-size: 1.2rem;
  max-width: 600px;
}

.fade-enter {
  opacity: 0;
  transform: translateY(20px);
}

.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 300ms ease, transform 300ms ease;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms ease;
}

.division-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin-bottom: 2rem;
}

.division-buttons button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border: none;
  border-radius: 6px;
  background-color: #444;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.division-buttons button:hover {
  background-color: #666;
}

.division-buttons .active {
  background-color: #0077cc;
}

.division-content {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
}

.division-block h3 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.division-block p {
  font-size: 1.2rem;
  line-height: 1.6;
}


